import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Observable } from 'rxjs/internal/Observable';
import { BehaviorSubject } from 'rxjs';
import { tap } from 'rxjs/operators';

// MODELS
import { User } from '../models/user.model';
import { JwtToken } from '../models/jwt-token.model';

@Injectable({
  providedIn: 'root'
})

export class UserService {
  // Création d'un Behavior Subject qui surveille les valeurs du token de connexion
  public jwtToken: BehaviorSubject<JwtToken> = new BehaviorSubject({
    isLogged: null,
    token: null
  });

  constructor(private http: HttpClient, private router: Router) {
    this.checkToken(); // on verifie la presence d'un token dans le local storage lors de l'instanciation du service
  }

   // Methode de verification du token dans le localstorage (API native JS)
  private checkToken(): void{
    const token = localStorage.getItem('jwt');
    if (token) {
      this.jwtToken.next({
        isLogged: true,
        token: token
      });
    } else {
      this.jwtToken.next({
        isLogged: false,
        token: null
      });
    }
  }

  // Methode envoyant une requete pour la creation d'un new user
  public register(user: User): Observable<User> {
    return this.http.post<User>('/request/register', user);
  }

  // Methode envoyant une requete pour la connexion d'un user avec recuparation du token cree cote serveur
  public login(credentials: {email: string, password: string}): Observable<string> {
    return this.http.post<string>('/request/login', credentials).pipe(
      tap( (token: string) => {
        this.jwtToken.next({ // Maj de l'observable avec une nouvelle valeur
          isLogged: true,
          token: token
        });
        // Stockage du token recupere depuis le serveur dans le localstorage du client
        localStorage.setItem('jwt', token);
      })
    );
  }

  // Methode de deconnexion (suppression du token)
  public logout(): void {
    // reset du behaviour subject JwtToken dans userService avec next()
    this.jwtToken.next({
      isLogged: false,
      token: null
    });
    // suppression du token du localStorage
    localStorage.removeItem('jwt');
    // redirection vers la page de connexion
    this.router.navigate(['/']);
  }
}
